import React from 'react'

import { AppLink } from './AppLink';
import styled from 'styled-components';


const AppPhoneStyles = styled(AppLink)`
  ${ props => (
  props.withIcon && `
    &::before: {
      content: '';
      background: url('/svg/phone.svg') no-repeat;
    }
  `)}
`

export const AppPhone = ({ phone, withIcon = true }) => (
  <AppPhoneStyles href={ `tel:${ phone }` } withIcon={ true }>
    { phone }
  </AppPhoneStyles>
)
